/*------------------------
	base class definition
-------------------------*/

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'nucleo';
  color: inherit;
  flex-shrink: 0;
  text-transform: none;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*------------------------
  change icon size
-------------------------*/

/* relative units */
.icon-sm {
  font-size: 0.8em;
}
.icon-lg {
  font-size: 1.2em;
}
/* absolute units */
.icon-16 {
  font-size: 16px;
}
.icon-32 {
  font-size: 32px;
}

/*------------------------
  spinning icons
-------------------------*/

.icon-is-spinning {
  animation: icon-spin 1s infinite linear;
}

@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/*------------------------
  rotated/flipped icons
-------------------------*/

.icon-rotate-90  {
	transform: rotate(90deg);
}

.icon-rotate-180 {
	transform: rotate(180deg);
}

.icon-rotate-270 {
	transform: rotate(270deg);
}

.icon-flip-y {
	transform: scaleY(-1);
}
.icon-flip-x {
	transform: scaleX(-1);
}

/*------------------------
	icons
-------------------------*/
@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

$icon-logo-github: unicode(ea02);
$icon-logo-linkedin: unicode(ea03);
$icon-logo-youtube: unicode(ea04);
$icon-logo-instagram: unicode(ea05);
$icon-right-arrow: unicode(ea06);

.icon-logo-github::before {
  content: $icon-logo-github;
}

.icon-logo-linkedin::before {
  content: $icon-logo-linkedin;
}

.icon-logo-youtube::before {
  content: $icon-logo-youtube;
}

.icon-logo-instagram::before {
  content: $icon-logo-instagram;
}

.icon-right-arrow::before {
  content: $icon-right-arrow;
}

/* COLORS */
$black: #0c080e;
$white: #ffffff;
$yellow: #ffca0d;
$green: #008281;
$grey: #dadada;
$darkgrey: #c0c0c0;
$red: #EB4A4B;

/* LAYOUT */
$max-width: 1100px;


* {
  font-family: 'Roboto-Light';
	line-height: 1;
	color: black;
}

.heading, h1, h2, h3, h4,
.paragraph, p {
	line-height: 1.5;
}

.heading--h1, h1 {
	font-size: 40px;
}

.heading--h2, h2 {
	font-size: 30px;
}

.heading--h3, h3 {
	font-size: 25px;
}

.heading--h4, h4 {
	font-size: 20px;
	font-family: 'Roboto-Bold';
}

.heading--h4, h4 {
	font-size: 20px;
	font-family: 'Roboto-Bold';
}

.heading--h5, h5 {
	font-size: 20px;
}

.paragraph, p {
	font-size: 18px;
}

.link-navigation {
	font-size: 18px;
}

.input, input,
.textarea, textarea {
	font-size: 18px;
	line-height: 1.5;
}



@mixin hidden {
	height: 0;
	width: 0;
	visibility: hidden;
}
* {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto-Thin';
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
}

main {
	min-height: calc(100vh - (100px + 390px));
}

@media (max-width: 750px) {

	main {
		min-height: calc(100vh - (75px + 600px));
	}

}
